/* Basic colors */
.test-btn {
  background-color: #000000;
  color: #ffffff;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.card {
  display: block;
  padding: 0.75rem;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.5);
  max-width: 400px;
  border-radius: 5px;
}
.card__title {
  font-size: 2rem;
  padding-bottom: 0.75rem;
  font-weight: bold;
}
.card__body__paragraph {
  font-size: 1rem;
}
.card__body__link {
  display: inline-block;
  border: 1px solid #4d4d4d;
  padding: 0.75rem;
  font-size: 1rem;
  margin: 0.75rem;
}
.card__body__link:hover {
  background: #000000;
  color: #ffffff;
}

.container {
  max-width: 90%;
  margin: 0 auto;
}
.container h2 {
  color: #326dee;
}
.container a {
  color: #326dee;
}
.container .error {
  color: #d32752;
  border: 2px solid #d32752;
  width: 200px;
  height: 40px;
}
.container .notify {
  color: #f6c31c;
  border: 2px solid #1ac888;
  padding: 0.75rem;
  width: 200px;
  height: 40px;
}
.container button {
  border: none;
  color: #ffffff;
  background: #326dee;
  padding: 0.75rem;
  margin: 0.75rem;
  border-radius: 20px;
}